<script>
import axios from "axios";
import html2canvas from 'html2canvas';
import QRCode  from "qrcodejs2"
import moment from "moment"

export default {
  name: 'HelloWorld',
  components: {
  },
  props: {
    msg: String
  },
  data(){
    return {
      htmls:[{title:'Êú™Áü•',html:'<p>Êï∞ÊçÆÊú™Áü•</p>'}],
      datalen:1,
      select:1,
      searchKeyword:'',
      dialogMsg:"",
      dialogVisible:false,
      dialogTitle:'ÊèêÁ§∫',
      bLink: '',
      xLink:'',
      yLink:'',
      isMobile:false,
      randomMovie: null,
      songci:[],
      isMuics:false,
      currentCi:null,
      allMovies:[],
      loading:true,
      innerVisible:false,
      innerVisibleMsg:'',
      dialogVisibleAbout:false,
    }
  },
  mounted(){
    const showAboutNum = [2,5,10,20,40,70,100,200]
    console.log('mounted')
    const accessNum = localStorage.getItem('accessNum')
    if(accessNum!==null&&!isNaN(accessNum)){
      let currNum = Number(accessNum) + 1
      if(showAboutNum.includes(currNum)){
         this.$nextTick(() => {
            this.about()
         })
      }
      localStorage.setItem('accessNum',currNum );
    }else{
      localStorage.setItem('accessNum', 1);
    }
  },
  created(){
    console.log('created')
    axios.get('/db/data.json').then(res => {
      if(res.data.length > 0){
          this.htmls = []
          this.datalen = res.data.length
      }

      for (let index = 0; index < res.data.length; index++) {
        const element = res.data[index];
        axios.get('/wechat-page/'+element).then(resx => {
           this.htmls.push({title:element.replace(".html",""),html:resx.data.replace(/<p><br\s\s\/><\/p>/g,'').replace(/<p><span style="font-size: \d\dpx;"><br\s\s\/><\/span><\/p>/g,'').replace(/<p style=""><br\s\s\/><\/p>/g,'')})
           if(this.datalen === this.htmls.length){
             this.htmls = this.htmls.sort((a, b) => b.title - a.title)
           }
         })
         axios.get('/db/'+element.replace(".html",".json")).then(resy => {
           const data = resy.data.map(x => {
              x.year = element.replace(".html","")
              if(x.title !== x.text){
                x.title = x.text
              }
              return x
             }).sort((a,b) => b.linkContent.ori_create_time - a.linkContent.ori_create_time)
             setTimeout(() => {
              this.loading = false
            },100)
           this.allMovies = this.allMovies.concat(data)
         })
      }
    })
    this.isMobile = this._isMobile()
    this.$nextTick(() => {
      this.$refs.keywordInput.focus()
    })
    axios.get('/db/songci300.json').then(res => {
      this.songci = res.data
    })
  },
  methods:{
    removeDefTitle(){
      let contents = document.getElementsByClassName('grid-content')
      contents.forEach(el => {
        el.removeChild(el.firstChild)
      })
    },
    nowTime(){
      return moment().format("YYYY-MM-DD HH:mm:ss")
    },
    showMsg(msg){
      this.dialogVisible = true
      this.dialogMsg = msg
    },
    handleClose(){
      this.dialogVisible = false
    },
    handleCloseInner(){
      this.innerVisible = false
    },
    handleClosed(){
      this.randomMovie = null
      this.currentCi = null
      this.dialogMsg = ""
      this.dialogTitle = 'ÊèêÁ§∫'
      this.isMuics = false
    },
    handleCloseAbout(){
      this.dialogVisibleAbout = false
    },
    handleClosedAbout(){

    },
    about(){
      this.dialogVisibleAbout = true
    },
    genImgUrl(movie){
      return '/cover/'+movie.coverLink
    },
    keywordInputSearch(queryString, cb){
      var results = queryString ? this.allMovies.filter(x => {
          return x.title.includes(queryString)|x.text.includes(queryString)
        }).map(x =>  {
          return {"value":x.title.replace(/\d{1,3}„ÄÅ/,''),'createTime':x.linkContent.create_time,'pageTitle':x.linkContent.title}}) 
          : this.allMovies.map(x => {return {"value":x.title.replace(/\d{1,3}„ÄÅ/,''),'createTime':x.linkContent.create_time,'pageTitle':x.linkContent.title}});
      // Ë∞ÉÁî® callback ËøîÂõûÂª∫ËÆÆÂàóË°®ÁöÑÊï∞ÊçÆ
      cb(results);

    },
    handerContentNoencode(html){
      return html.replace(/<section.*?section>/g,'')
      .replace(/<iframe.*?iframe>/g,'')
      .replace(/<img.*?>/g,'')
      .replace(/<p.*?>.*?[È´ò|Â§á].*[Ê∏Ö|Áî®].*Êí≠.*Êîæ.*[Âú∞|ËßÜ].*[ÂùÄ|È¢ë].*?<\/p>/,'')
      .replace(/<p.*?Á•®Âúàvlog.*?>.*?<\/p>/,'')
    },
     _isMobile() {
      let flag = navigator.userAgent.match(/(phone|pad|pod|iPhone|iPod|ios|iPad|Android|Mobile|BlackBerry|IEMobile|MQQBrowser|JUC|Fennec|wOSBrowser|BrowserNG|WebOS|Symbian|Windows Phone)/i)
      return flag;
    },
    openMovieDetail(num=-1,movie){
      if (num>=0){
        this.randomMovie = this.allMovies[num]
      }else{
         this.randomMovie = movie
      }
      this.dialogVisible = true
    },
    randomOpen(){
      //const movies = document.querySelectorAll('.grid-content  a')
      const num = Number(Math.floor(Math.random() * (this.allMovies.length)))
      this.dialogTitle = 'ÊâãÊ∞î‰∏çÈîô'
      this.openMovieDetail(num)
    },
    songCi(){
      const num = Math.floor(Math.random() * (this.songci.length))
      this.currentCi = this.songci[num]
      this.dialogTitle = '„Ää'+this.currentCi.rhythmic+'„Äã'
      this.dialogVisible = true
    },
    music(){
       this.isMuics = true
       this.dialogTitle = 'Êù•È¶ñÈü≥‰πê'
       this.dialogVisible = true
    },
    qrcode (domId,link) {
        new QRCode(domId, {
            width: 124,
            height: 124,        // È´òÂ∫¶
            text: link,   // ‰∫åÁª¥Á†ÅÂÜÖÂÆπ
            // render: 'canvas' ,   // ËÆæÁΩÆÊ∏≤ÊüìÊñπÂºèÔºàÊúâ‰∏§ÁßçÊñπÂºè tableÂíåcanvasÔºåÈªòËÆ§ÊòØcanvasÔºâ
            // background: '#f0f',   // ËÉåÊôØËâ≤
            // foreground: '#ff0'    // ÂâçÊôØËâ≤
        })
    },
    shareImg(selector,link){
      this.innerVisibleMsg = 'ÂõæÁâáÁîüÊàê‰∏≠...'
      this.innerVisible = true
      const that = this
      that.$nextTick(() => {
        if(link!==undefined && link!==""){
          that.qrcode('qrcode',link)
        }
        that.$nextTick(() => {
          html2canvas(document.querySelector(selector),{cale: 6}).then(function(canvas) {
              that.innerVisibleMsg = 'ÂõæÁâáÁîüÊàêÊàêÂäüÔºÅÂèØÈïøÊåâÊàñÂè≥ÈîÆ‰øùÂ≠òÂõæÁâá'
              const dom_img = document.createElement("img");
              dom_img.src = canvas.toDataURL("image/jpeg")
              dom_img.classList.add('el-image__inner')
              dom_img.style = 'object-fit: cover;'
              document.querySelector("#inner-body-img-box").appendChild(dom_img);
          });
        })
      })
    },
    searchDoms(){
      let doms = document.querySelectorAll('a[textvalue*="'+this.searchKeyword+'"]')
      const movie = this.allMovies.filter((x) => x.title.includes(this.searchKeyword))
      this.bLink = 'https://space.bilibili.com/149558293/search/video?keyword='+this.searchKeyword
      this.xLink = 'https://www.ixigua.com/search/Ë∂äÂì•ËØ¥ÁîµÂΩ±'+this.searchKeyword
      this.yLink = 'https://www.youtube.com/channel/UChgCVolsF6L7DWmOpWKSkMA/search?query='+this.searchKeyword
      if(movie.length === 0){
        this.showMsg('Ê≤°ÊúâÊâæÂà∞„Ää'+this.searchKeyword+'„ÄãÁöÑËß£ËØ¥ÔºåËµ∂Á¥ßËÆ©Ë∂äÂì•Ëß£ËØ¥üòÇ,ÊàñËÄÖÂéª‰∏ãÈù¢ÊâæÊâæ')
      } else if(movie.length === 1) {
        this.dialogTitle = 'ÊâæÂà∞‰∫Ü‰∏Ä‰∏™Ëß£ËØ¥'
        this.openMovieDetail(-1,movie[0])
      }
      doms.forEach(x => {
        x.classList.add('search')
        x.scrollIntoView({block: "center"})
      })
    }
  }
}
</script>

